<template>
  <el-drawer :before-close="onBeforeClose" :title="isAdd?'':''" :visible.sync="visible" @open="onDrawerOpen" ref="drawer" size="35%">
    <div class="d-flex flex-column p-4 h-100">
      <el-form :model="formData" :rules="rules" @submit.stop.prevent="onSubmit" class="flex-fill" label-width="85px" ref="ruleForm">
        <el-form-item label="" prop="name">
          <el-input autocomplete="off" placeholder="" v-model="formData.name"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="routeUrl">
          <el-input placeholder="" required v-model="formData.routeUrl"></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input placeholder="" v-model="formData.controller"></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input placeholder="" v-model="formData.action"></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input :autosize="{ minRows: 2, maxRows: 4}" placeholder="" type="textarea" v-model="formData.description"></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input placeholder="" type="number" v-model="formData.orderSort"></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-switch v-model="formData.isEnabled"></el-switch>
        </el-form-item>
      </el-form>
      <div class="d-flex">
        <el-button @click="onCancle" class="flex-fill"> </el-button>
        <el-button :loading="loading" @click="onSubmit" class="flex-fill" type="primary">{{ loading ? ' ...' : ' ' }}</el-button>
      </div>
    </div>
  </el-drawer>
</template>

<script>
import { isEmpty } from '../../common/index'
import moduleApi from '../../api/admin/module'
export default {
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    id: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      loading: false,
      formData: {
        id: '',
        name: '',
        routeUrl: '',
        controller: '',
        action: '',
        description: '',
        orderSort: '',
        isEnabled: true
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        routeUrl: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    onSubmit() {
      var that = this
      if (isEmpty(that.id)) {
        that.loading = true
        moduleApi.add(that.formData)
          .then(() => {
            that.loading = false
            that.onSaveSuccess()
            that.onCancle()
          })
          .catch(() => {
            that.loading = false
          })
      } else {
        that.loading = true
        moduleApi.update(that.formData)
          .then(() => {
            that.loading = false
            that.onSaveSuccess()
            that.onCancle()
          })
          .catch(() => {
            that.loading = false
          })
      }
    },
    onCancle() {
      this.$refs.drawer.closeDrawer()
    },
    getDetail(id) {
      var that = this
      moduleApi.getDetail(id)
        .then(res => {
          that.formData = res.data
        })
    },
    onBeforeClose() {
      this.$emit('update:visible', false)
    },
    onDrawerOpen() {
      if (!isEmpty(this.id)) {
        this.getDetail(this.id)
      } else if (this.$refs.ruleForm) {
        this.$refs.ruleForm.resetFields()
      }
    },
    onSaveSuccess() {
      this.$emit('success')
    }
  },
  computed: {
    isAdd() {
      return isEmpty(this.id)
    },
    showdrawer() {
      return this.visible
    }
  }
}
</script>
<style lang="scss">
#module-sidebar {
  .form-row {
    align-items: center;
  }
}
</style>
